import { Box, Grid } from '@mui/material';
import React, { useState, useEffect } from 'react';
import Controls from '../../components/controlls/Controls';
import { useForm, Form } from '../../components/useForm';
import * as employeeService from '../../service/employeeService';


const genderItems = [
    { id: 'male', title: 'Male' },
    { id: 'female', title: 'Female' },
    { id: 'other', title: 'Other' },
]

const initialFValues = {
    id: 0,
    staffID: '',
    firstName: '',
    fullName: '',
    lastName: '',
    qualification: '',
    academicRank: '',
    posision: '',
    email: '',
    phone: '',
    gender: 'male',
    hireDate: new Date(),
    isFullTime: false,
}

const StaffForm = (props) => {
    const { addOredit, recordForEdit } = props;

    const validate = (fieldValues = values) => {
        let temp = { ...errors }
        if ('firstName' in fieldValues)
            temp.fullName = fieldValues.fullName ? "" : "This field is required."
        if ('lastName' in fieldValues)
            temp.fullName = fieldValues.fullName ? "" : "This field is required."
        if ('email' in fieldValues)
            temp.email = (/$^|.+@.+..+/).test(fieldValues.email) ? "" : "Email is not valid."
        if ('phone' in fieldValues)
            temp.phone = fieldValues.phone.length > 9 ? "" : "Minimum 10 numbers required."
        if ('academicRank' in fieldValues)
            temp.departmentId = fieldValues.departmentId.length != 0 ? "" : "This field is required."
        setErrors({
            ...temp
        })

        if (fieldValues == values)
            return Object.values(temp).every(x => x == "")
    }

    const {
        values,
        setValues,
        errors,
        setErrors,
        handleInputChange,
        resetForm
    } = useForm(initialFValues, true, validate);

    const handleSubmit = e => {
        e.preventDefault()
         console.log(' kkkkk');
        // if (validate()) {
            console.log(' kkkkk');
            addOredit(values, resetForm)
        // }
    }

    useEffect(() => {
        if (recordForEdit != null)
            setValues({
                ...recordForEdit
            })
    }, [recordForEdit])

    return (
     
            <Form onSubmit={handleSubmit}>
                <Grid container >
                    <Grid item xs={6} >
                        <Controls.Input
                            name="firstName"
                            label="First Name"
                            value={values.firstName}
                            onChange={handleInputChange}
                            error={errors.firstName}
                            size="small"
                            fullWidth
                            style={{ margin: 10 }}
                        />
                        <Controls.Input
                            name="lastName"
                            label="Last Name"
                            value={values.lastName}
                            onChange={handleInputChange}
                            error={errors.lastName}
                            size="small"
                            fullWidth
                            style={{ margin: 10 }}
                        />
                        <Controls.Input
                            name="staffID"
                            label="ID"
                            value={values.staffID}
                            onChange={handleInputChange}
                            error={errors.staffID}
                            size="small"
                            fullWidth
                            style={{ margin: 10 }}
                        />
                       
 

                        <Controls.Input
                            label="Email"
                            name="email"
                            value={values.email}
                            onChange={handleInputChange}
                            error={errors.email}
                            size="small"
                            style={{ margin: 10, mb: 10 }}
                        />
                          <Controls.DatePicker
                            name="hireDate"
                            label="Hire Date"
                            value={values.hireDate}
                            onChange={handleInputChange}
                            size="small"
                            style={{ margin: 10 }}
                        />
                    </Grid>
                    <Grid item xs={6} >
                    <Controls.Input
                            name="qualification"
                            label="Qualification"
                            value={values.qualification}
                            onChange={handleInputChange}
                            error={errors.qualification}
                            size="small"
                            fullWidth
                            style={{ margin: 10 }}
                        />
                        <Controls.Select
                            name="academicRank"
                            label="Academic Rank"
                            options={employeeService.getAcademicRankCollection()}
                            value={values.academicRank}
                            onChange={handleInputChange}
                            error={errors.academicRank}
                            size="small"
                            style={{ margin: 10 }}
                        />

                       
                        <Controls.RadioGroup
                            name="gender"
                            label="Gender"
                            value={values.gender}
                            onChange={handleInputChange}
                            items={genderItems}
                            style={{ margin: 10 }}

                        />
                       
                        <Controls.CheckBox
                            name="isFullTime"
                            label="Full-time Employee"
                            value={values.isFullTime}
                            onChange={handleInputChange}
                            style={{ margin: 10 }}
                        />

                        <div>
                        <Controls.Button
                                text="Reset"
                                onClick={resetForm} />
                            <Controls.Button
                                type="submit"
                                text="Submit" />
                        </div>
                    
                    </Grid>
                </Grid>
            </Form>
      


    )
}
export default StaffForm;