import { styled, Table, TableCell, TableHead, TablePagination, TableRow, TableSortLabel } from '@mui/material'
import React, { useState } from 'react'

export default function useTable(records, headCells, filterFn) {

  const pages = [5, 10, 25]
  const [page, setPage] = useState(0)
  const [rowsPerPage, setRowsPerPage] = useState(pages[page])
  const [order, setOrder] = useState()
  const [orderBy, setOrderBy] = useState()

  const TblContainer = props => (
    <Table sx={{
      marginTop: 4,
    
      '& thead th': {
        fontWeight: '600',
        color: '#333996',
        backgroundColor: '#3c44b126',
      },
      '& tbody td': {
        fontWeight: '300',
      },
      '& tbody tr:hover': {
        backgroundColor: '#fffbf2',
        cursor: 'pointer',
      },
    }}>
      {props.children}
    </Table>
  )


  const handleSortRequest = (cellId) => {
    const isAsc = orderBy === cellId && order === "asc";
    setOrder(isAsc ? 'desc' : 'asc');
    setOrderBy(cellId);
  }
 
  const handleChangePage = (event, newPage) => {
    setPage(newPage);
  }

  const handleChangeRowsPerPage = event => {
    setRowsPerPage(parseInt(event.target.value, 10));
    setPage(0);
  }
  function stableSort(array, comparator){
    const stabilizedThis = array.map((el, index) => [el, index]);
    stabilizedThis.sort((a, b) => {
        const order = comparator(a[0], b[0]);
        if (order !== 0) return order;
        return a[1] - b[1];
    });

    return stabilizedThis.map((el) => el[0]);
  }
  function getComparator(order, orderBy) {
    return order === 'desc'
        ? (a, b) => descendingComparator(a, b, orderBy)
        : (a, b) => -descendingComparator(a, b, orderBy);
}
function descendingComparator(a, b, orderBy) {
  if (b[orderBy] < a[orderBy]) {
      return -1;
  }
  if (b[orderBy] > a[orderBy]) {
      return 1;
  }
  return 0;
}

  const recordsAfterPagingAndSorting = () => {
    return (
     stableSort(filterFn.fn(records), getComparator(order,orderBy)).slice(page * rowsPerPage, (page * rowsPerPage) + rowsPerPage)
    )
  }
  const TblHead = props => {
    return (<TableHead>
      <TableRow>
        {
          headCells.map(headCell => (
            <TableCell key={headCell.id} sortDirection={orderBy === headCell.id ? order : false}>
            {headCell.disableSorting ? headCell.label :
              <TableSortLabel
                onClick={() => handleSortRequest(headCell.id)}
                direction={orderBy === headCell.id ? order : 'asc'}
                active={orderBy===headCell.id}>
                {headCell.label}
              </TableSortLabel>
            }
            </TableCell>
          ))
        }
      </TableRow>
    </TableHead>)
  }
  const TblPagenation = props => (
    <TablePagination
      component="div"
      page={page}
      rowsPerPageOptions={[5, 10, 25, { label: 'All', value: -1 }]}
      rowsPerPage={rowsPerPage}
      count={records.length}
      onPageChange={handleChangePage}
      onRowsPerPageChange={handleChangeRowsPerPage}
    />

  )
  return {
    TblContainer,
    TblHead,
    TblPagenation,
    recordsAfterPagingAndSorting
  }
}

